.modal {
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 2px solid #7176f7;
    border-radius: $radius;
    width: 300px;
    display: grid;
    grid-gap: 20px;
    & > * {
        display: block;
        margin: 0 auto;
    }
    &__button {
        width: 150px;
        margin: 0 auto !important;
    }
}
